.links {
    height: calc(100vh - 200px);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-style: italic;
    
    @media only screen and (max-width: 1100px) {
    flex-direction: column;
    padding-top: 30px;
    }

    @mixin linkCardAMA {
        border: 3px groove #000;
        border-radius: 22px;
        height: 180px;
        width: 360px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 0 50px;
        box-sizing: content-box;
        background: linear-gradient(to right, #2196f3, #26c6da, #43a047, #eeff41, #f9a825, #ff5722);
        
        @media only screen and (max-width: 1280px) {
        flex-direction: column;
        padding: 5px;
        margin: 15px;
        width: 300px;
        }

    
        span {
            display: flex;
            flex-direction: row;
            font-weight: 900;
            align-items: center;
            font-size: 0.55cm;
            margin-top: -20px;
            font-family: Verdana;
        }

        a {
            font-weight: 900;
            font-size: 0.65cm;
            font-family: Verdana, Geneva, Tahoma, sans-serif;
            color: black;
            text-decoration: none;
            margin-top: 10px;

            &:hover {
                color: #e6e1e1;
                transform: scale(1.2);
                transition: .5s ease-in;
            }
        }
    }

    .linkCard1 {
        @include linkCardAMA();
    }
    .linkCard2 {
        @include linkCardAMA();
    }
    .linkCard3 {
        @include linkCardAMA();
    }
}